import React, { Component } from 'react';
import Layout from "../components/layout"
import SEO from "../components/seo"
import { Navbar, Nav, Col, Row } from 'react-bootstrap'
import Interweave from 'interweave'

class Template extends Component {


  render() {
    const { markdownRemark: page } = this.props.data;
    if (typeof window !== `undefined`){
      var el = document.createElement('html');
      el.innerHTML = page.tableOfContents;
      var TOC = el.getElementsByTagName('a')
    }
    return (
      <Layout>
        <Row className="flex-fill m-0" style={{ minHeight: 0 }}>
          <Col
            sm={12}
            md={9}
            className="pl-4 pr-0">
            <SEO title={`Docs | ${page.frontmatter.title}`} />
            <div className="page">
              <header>
                <h1>{page.frontmatter.title}</h1>
                <span>{page.frontmatter.baseline}</span>
              </header>
              <Interweave content={page.html}></Interweave>
            </div>
          </Col>
          <Col
            sm={0}
            md={3}
            className="mh-100 p-0 border-left">
            <Navbar bg="white" className="mh-100 pt-4" sticky="top">
              <Nav defaultActiveKey="/home" className="flex-column">
                {typeof window !== `undefined`?
                page.headings.map((item, id) => (
                  item.depth <= 2 ? 
                    item.depth == 1? <Nav.Link href={TOC[id].getAttribute("href")} className="p-0">{TOC[id].text}</Nav.Link>
                    :<Nav.Link href={TOC[id].getAttribute("href")} className="p-0">&emsp;{TOC[id].text}</Nav.Link>
                    : null
                )): null}
              </Nav>
            </Navbar>
          </Col>
        </Row>
      </Layout>
    );
  }
}
export default Template
export const pageQuery = graphql`
  query DocsByPath($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      tableOfContents(
        absolute: false
        maxDepth: 5
      )
      headings {
        depth
        value
      }
      frontmatter {
        path
        title
      }
    }
  }
`
  ;